<template>
  <div id="contentList">
    <div v-if="contentShow" class="loading-content">
      <img src="../assets/picture/loading_green.c0f9be0.gif" alt=""/>
    </div>
    <!--  -->
    <div v-else>
      <div class="contentList-box" v-for="items in items">
        <div class="pull-left contentList-title">
          <h2>{{items.contentTitle}}</h2>
          <p>{{items.contentTime}}</p>
        </div>
        <img class="pull-right contentList-img" :src="items.contentImg" alt=""/>
        <div class="contentList-footer clearfix" style="clear:both">
          <span class="pull-left">{{items.contentType}}</span>
          <span class="pull-right">{{items.contentDesc}}</span>
        </div>
      </div>
    </div>
    {{contentListShow}}
  </div>
</template>

<script>
export default {
  data:function(){
    return {
      contentShow: true,
      items:[
        {
          contentTitle: "teamLab",
          contentTime: "201752020171119",
          contentImg: require("../assets/image/462df4bd746c32b.jpg"),
          contentType: "",
          contentDesc: ""
        },
        {
          contentTitle: "AR",
          contentTime: "",
          contentImg: require("../assets/image/131875877d7f285.jpg"),
          contentType: "",
          contentDesc: ""
        },
        {
          contentTitle: "",
          contentTime: "2017917",
          contentImg: require("../assets/image/e74003452677a41.jpg"),
          contentType: "",
          contentDesc: ""
        },
        {
          contentTitle: " ",
          contentTime: "21",
          contentImg: require("../assets/image/2efa2c1d1c5f87d.jpg"),
          contentType: "",
          contentDesc: " "
        },
        {
          contentTitle: "",
          contentTime: "95 ",
          contentImg: require("../assets/image/8d7b1d60b3141e8.jpg"),
          contentType: "",
          contentDesc: ""
        },
        {
          contentTitle: "2",
          contentTime: "_",
          contentImg: require("../assets/image/eac04af8f200983.jpg"),
          contentType: "",
          contentDesc: ""
        }
      ]
    }
  },
  computed:{
    contentListShow:function (){
      var self = this;
      setTimeout(changeShow,3000);
      function changeShow(){
        self.contentShow = false;
      }
    }
  }

}
</script>

<style scoped>
#contentList {
  text-align: center;
}

.loading-content {
  margin-top: 25px;
  text-align: center;
}

.loading-content img {
  width: 3rem;
}

#contentList .contentList-box{
  padding: 30px 0;
  border-bottom:1px solid #e3e3e3;
}

#contentList .contentList-title {
  text-align: left;
  width: 70%;
}

#contentList .contentList-title h2 {
  color:#333;
  font-size: 1.6rem;
  font-weight: bold;
  margin-bottom: 10px;
}

#contentList .contentList-title p {
  font-size: 1.3rem;
  color:#aaa;
}

#contentList .contentList-img {
  width: 25%;
}

#contentList .contentList-footer {
  padding-top: 15px;
  font-size: 1.2rem;
  color:#aaa;
}

</style>



